* {
  margin: 0;
  padding: 0;
}

:root {
  --primary: #ff6f00;
  --secondary: red;
}

.header {
  background-color: var(--primary);
  color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  padding: 5px 10px;
  display: flex;
}

.logo-contain {
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.logo-container {
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  width: 4rem;
  height: 8vh;
  margin: 10px;
  overflow: hidden;
}

.logoName {
  color: #fff;
  font-family: Times New Roman, Times, serif;
  font-size: 2.5rem;
}

.logo {
  height: 90%;
  margin: 2px;
}

.nav-items {
  padding: 0 20px;

  & ul {
    list-style: none;
    display: flex;

    & li {
      margin: 10px;
      padding: 10px;
      font-size: x-large;
    }
  }
}

.search {
  padding: 10px;
}

.shimmerSearch {
  background-color: #80808033;
  width: 40vw;
  height: 6vh;
}

.restaurant-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.res-card {
  background-color: #fcfcfc;
  border-radius: 20px;
  width: 273px;
  height: 253px;
  margin: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 1px 10px #80808033;

  & h3 {
    text-align: center;
  }

  & h4 {
    color: gray;
    text-align: center;
    size: 0;
  }
}

.resName {
  color: #fff;
  text-shadow: 0 0 10px #000;
  width: 100%;
  font-size: 1.3rem;
  position: absolute;
  bottom: 80px;
}

.resData {
  font-size: .8rem;

  & .ratingBG {
    background-color: green;
  }
}

.res-card:hover {
  cursor: pointer;
  box-shadow: 0 5px 10px #80808080;
}

.res-logo {
  border-radius: 10px;
  width: 100%;
  height: 70%;
}

.footer {
  background-color: var(--primary);
  height: 6vh;
  position: static;
  bottom: 0;
}

.resDataContainer {
  justify-content: space-evenly;
  margin-top: 1rem;
  display: flex;
}

#resDataCuisines {
  text-align: center;
}

.restaurantNameInput {
  border: 0;
  width: 40vw;
  padding: 5px;
  font-size: 1rem;
}

.restaurantNameInput:focus {
  outline: none;
}

.searchBtn {
  color: var(--primary);
  background-color: #0000;
  border: 0;
  padding: 5px;
  font-size: 1rem;
}

.searchDiv {
  border: 1px solid #000;
  justify-content: center;
  width: fit-content;
  padding: .5rem;
  display: flex;
}

.search, .searchShimmer {
  justify-content: center;
  margin-top: 1rem;
  display: flex;
}

.filterBtn {
  cursor: pointer;
  width: 10vw;
  height: 4vh;
  margin: 10px;
  padding: 5px;
}

.shimmer-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;

  & .shimmer-card {
    background-color: #80808033;
    border-radius: 20px;
    width: 273px;
    height: 253px;
    margin: 10px;
  }
}

.btnShimmer {
  background-color: #80808033;
  width: 10vw;
  height: 4vh;
  margin: 10px;
  padding: 5px;
}

.ratingColor {
  border-radius: 5px;
  padding: 3px;

  & h4 {
    color: #fff;
  }
}

.loginBtn {
  cursor: pointer;
  font-size: inherit;
  color: var(--primary);
  background-color: #fff;
  border: 0;
  border-radius: 10px;
  padding: 5px 20px;
}
